<template>
  <div class="skill-item">
    <label class="skill-type">
      {{ Type }}
    </label>
    <div class="skill-desc">
      <div data-aos="fade-up" :data-aos-duration="AOS.Dur" :data-aos-offset="AOS.Offset">
        {{ Desc }}
      </div>
    </div>
  </div>
</template>

<script>
import '@/components/skill/skill.scss';
import AOS from 'aos';
import 'aos/dist/aos.css';

export default {
  name: 'Header',
  props: {
    Type: String,
    Desc: String,
  },
  data() {
    return {
      AOS: {
        Offset: '130',
        Dur: '1200',
      },
    };
  },
  mounted() {
    AOS.init();
  },
  created() {
    window.addEventListener('resize', this.HandleResize);
    this.HandleResize();
  },
  destroyed() {
    window.removeEventListener('resize', this.HandleResize);
  },
  computed: {},
  methods: {
    HandleResize() {
      let width = window.innerWidth;
      if (width > 1200) {
        this.AOS.Offset = '130';
      } else {
        this.AOS.Offset = '0';
      }
    },
  },
  components: {},
};
</script>
