<template>
  <div class="work-item">
    <div class="work-header">
      <label class="work-company">
        {{ Info.Company }}
      </label>
      <label class="work-time">
        {{ `${Info.StartTime} - ${Info.EndTime} ${Info.TotalTime}` }}
      </label>
      <div class="clear-both"></div>
    </div>
    <div class="work-desc">
      <div class="work-title" data-aos="fade-up" :data-aos-duration="AOS.Dur" :data-aos-offset="AOS.Offset">
        {{ `職稱：${Info.Title}` }}
      </div>
      <div class="work-skill" data-aos="fade-up" :data-aos-duration="AOS.Dur" :data-aos-offset="AOS.Offset">
        {{ `${Info.Skill}` }}
      </div>
      <div
        class="work-flow"
        data-aos="fade-up"
        :data-aos-duration="AOS.Dur"
        :data-aos-offset="AOS.Offset"
        v-html="Info.Desc"
      ></div>
    </div>
  </div>
</template>

<script>
import '@/components/work/work.scss';
import AOS from 'aos';
import 'aos/dist/aos.css';

export default {
  name: 'Header',
  props: {
    Info: Object,
  },
  data() {
    return {
      AOS: {
        Offset: '130',
        Dur: '1200',
      },
    };
  },
  mounted() {
    AOS.init();
  },
  created() {
    window.addEventListener('resize', this.HandleResize);
    this.HandleResize();
  },
  destroyed() {
    window.removeEventListener('resize', this.HandleResize);
  },
  computed: {},
  methods: {
    HandleResize() {
      let width = window.innerWidth;
      if (width > 1200) {
        this.AOS.Offset = '130';
      } else {
        this.AOS.Offset = '0';
      }
    },
  },
  components: {},
};
</script>
